@import url(https://fonts.googleapis.com/css?family=Lato:400,700%7CMerriweather:300,700%7CSource+Code+Pro:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*,
*:after,
*:before {
    box-sizing: inherit
}

html {
    box-sizing: border-box;
    font-size: 62.5%
}

body {
    color: #212121;
    background-color: #fafafa;
    font-family: Merriweather, Georgia, serif;
    font-size: 1.6em;
    font-weight: 300;
    line-height: 1.8em
}

@media only screen and (max-width:768px) {
    body {
        font-size: 1.6em;
        line-height: 1.6em
    }
}

a {
    font-weight: 300;
    color: #1565c0;
    text-decoration: none
}

a:hover {
    text-decoration: underline
}

p {
    margin: 2rem 0
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: Lato, Helvetica, sans-serif;
    font-weight: 700;
    color: #000;
    margin: 6.4rem 0 3.2rem
}

h1 {
    font-size: 3.2rem;
    line-height: 3.6rem
}

@media only screen and (max-width:768px) {
    h1 {
        font-size: 3rem;
        line-height: 3.4rem
    }
}

h2 {
    font-size: 2.8rem;
    line-height: 3.2rem
}

@media only screen and (max-width:768px) {
    h2 {
        font-size: 2.6rem;
        line-height: 3rem
    }
}

h3 {
    font-size: 2.4rem;
    line-height: 2.8rem
}

@media only screen and (max-width:768px) {
    h3 {
        font-size: 2.2rem;
        line-height: 2.6rem
    }
}

h4 {
    font-size: 2.2rem;
    line-height: 2.6rem
}

@media only screen and (max-width:768px) {
    h4 {
        font-size: 2rem;
        line-height: 2.4rem
    }
}

h5 {
    font-size: 2rem;
    line-height: 2.4rem
}

@media only screen and (max-width:768px) {
    h5 {
        font-size: 1.8rem;
        line-height: 2.2rem
    }
}

h6 {
    font-size: 1.8rem;
    line-height: 2.2rem
}

@media only screen and (max-width:768px) {
    h6 {
        font-size: 1.6rem;
        line-height: 2rem
    }
}

b,
strong {
    font-weight: 700
}

.highlight>div,
.highlight>pre {
    margin: 0 0 2rem;
    padding: 1rem;
    border-radius: 1rem
}

pre {
    display: block;
    font-family: source code pro, lucida console, monospace;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.6rem;
    overflow-x: auto;
    margin: 0
}

pre code {
    display: inline-block;
    background-color: inherit;
    color: inherit
}

code {
    font-family: source code pro, lucida console, monospace;
    font-size: 1.6rem;
    font-weight: 400;
    background-color: #e0e0e0;
    color: #212121
}

blockquote {
    border-left: 2px solid #e0e0e0;
    padding-left: 2rem;
    line-height: 2.2rem;
    font-weight: 400;
    font-style: italic
}

th,
td {
    padding: 1.6rem
}

table {
    border-collapse: collapse
}

table td,
table th {
    border: 2px solid #000
}

table tr:first-child th {
    border-top: 0
}

table tr:last-child td {
    border-bottom: 0
}

table tr td:first-child,
table tr th:first-child {
    border-left: 0
}

table tr td:last-child,
table tr th:last-child {
    border-right: 0
}

img {
    max-width: 100%
}

figure {
    text-align: center
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 100%
}

.container {
    margin: 0 auto;
    max-width: 90rem;
    width: 100%;
    padding-left: 2rem;
    padding-right: 2rem
}

.fab {
    font-weight: 400
}

.fas {
    font-weight: 700
}

.float-right {
    float: right
}

.float-left {
    float: left
}

.fab {
    font-weight: 400
}

.fas {
    font-weight: 900
}

.content {
    flex: 1 1;
    display: flex;
    margin-top: 1.6rem;
    margin-bottom: 3.2rem
}

.content article header {
    margin-top: 6.4rem;
    margin-bottom: 3.2rem
}

.content article header h1 {
    font-size: 4.2rem;
    line-height: 4.6rem;
    margin: 0
}

@media only screen and (max-width:768px) {
    .content article header h1 {
        font-size: 4rem;
        line-height: 4.4rem
    }
}

.content article footer {
    margin-top: 4rem
}

.content article footer .see-also {
    margin: 3.2rem 0
}

.content article footer .see-also h3 {
    margin: 3.2rem 0
}

.content .post .post-title {
    margin-bottom: .75em
}

.content .post .post-meta i {
    text-align: center;
    width: 1.6rem;
    margin-left: 0;
    margin-right: .5rem
}

.content .post .post-meta .date .posted-on {
    margin-left: 0;
    margin-right: 1.5rem
}

.content figure {
    margin: 0;
    padding: 0
}

.content figcaption p {
    text-align: center;
    font-style: italic;
    font-size: 1.6rem;
    margin: 0
}

.avatar img {
    width: 20rem;
    height: auto;
    border-radius: 50%
}

@media only screen and (max-width:768px) {
    .avatar img {
        width: 10rem
    }
}

.list ul {
    margin: 3.2rem 0;
    list-style: none;
    padding: 0
}

.list ul li {
    font-size: 1.8rem
}

@media only screen and (max-width:768px) {
    .list ul li {
        margin: 1.6rem 0
    }
}

.list ul li .date {
    display: inline-block;
    width: 20rem;
    text-align: right;
    margin-right: 3rem
}

@media only screen and (max-width:768px) {
    .list ul li .date {
        display: block;
        text-align: left
    }
}

.list ul li .title {
    font-size: 1.8rem;
    color: #212121;
    font-family: Lato, Helvetica, sans-serif;
    font-weight: 700
}

.list ul li .title:hover,
.list ul li .title:focus {
    color: #1565c0
}

.centered {
    display: flex;
    align-items: center;
    justify-content: center
}

.centered .about {
    text-align: center
}

.centered .about h1 {
    margin-top: 2rem;
    margin-bottom: .5rem
}

.centered .about h2 {
    margin-top: 1rem;
    margin-bottom: .5rem;
    font-size: 2.3rem
}

@media only screen and (max-width:768px) {
    .centered .about h2 {
        font-size: 2rem
    }
}

.centered .about ul {
    list-style: none;
    margin: 3rem 0 1rem;
    padding: 0
}

.centered .about ul li {
    display: inline-block;
    position: relative
}

.centered .about ul li a {
    color: #212121;
    text-transform: uppercase;
    margin-left: 1rem;
    margin-right: 1rem;
    font-size: 1.6rem
}

.centered .about ul li a:hover {
    color: #1565c0
}

@media only screen and (max-width:768px) {
    .centered .about ul li a {
        font-size: 1.4rem
    }
}

.centered .about ul li a i {
    font-size: 3.2rem
}

.centered .error {
    text-align: center
}

.centered .error h1 {
    margin-top: 2rem;
    margin-bottom: .5rem;
    font-size: 4.6rem
}

@media only screen and (max-width:768px) {
    .centered .error h1 {
        font-size: 3.2rem
    }
}

.centered .error h2 {
    margin-top: 2rem;
    margin-bottom: 3.2rem;
    font-size: 3.2rem
}

@media only screen and (max-width:768px) {
    .centered .error h2 {
        font-size: 2.8rem
    }
}

.navigation {
    height: 6rem;
    width: 100%
}

.navigation a,
.navigation span {
    display: inline;
    font-size: 1.6rem;
    font-family: Lato, Helvetica, sans-serif;
    font-weight: 700;
    line-height: 6rem;
    color: #212121
}

.navigation a:hover {
    color: #1565c0
}

.navigation .navigation-title {
    letter-spacing: .1rem;
    text-transform: uppercase
}

.navigation .navigation-list {
    float: right;
    list-style: none;
    margin-bottom: 0;
    margin-top: 0
}

@media only screen and (max-width:768px) {
    .navigation .navigation-list {
        position: absolute;
        top: 6rem;
        right: 0;
        z-index: 5;
        visibility: hidden;
        opacity: 0;
        padding: 0;
        max-height: 0;
        width: 100%;
        background-color: #fafafa;
        border-top: solid 2px #e0e0e0;
        border-bottom: solid 2px #e0e0e0;
        -webkit-transition: opacity .25s, max-height .15s linear;
        transition: opacity .25s, max-height .15s linear
    }
}

.navigation .navigation-list .navigation-item {
    float: left;
    margin: 0;
    position: relative
}

@media only screen and (max-width:768px) {
    .navigation .navigation-list .navigation-item {
        float: none!important;
        text-align: center
    }
    .navigation .navigation-list .navigation-item a,
    .navigation .navigation-list .navigation-item span {
        line-height: 5rem
    }
}

.navigation .navigation-list .navigation-item a,
.navigation .navigation-list .navigation-item span {
    margin-left: 1rem;
    margin-right: 1rem
}

@media only screen and (max-width:768px) {
    .navigation .navigation-list .menu-separator {
        border-top: 2px solid #212121;
        margin: 0 8rem
    }
    .navigation .navigation-list .menu-separator span {
        display: none
    }
}

.navigation #menu-toggle {
    display: none
}

@media only screen and (max-width:768px) {
    .navigation #menu-toggle:checked+label {
        color: #e0e0e0
    }
    .navigation #menu-toggle:checked+label+ul {
        visibility: visible;
        opacity: 1;
        max-height: 100rem
    }
}

.navigation .menu-button {
    display: none
}

@media only screen and (max-width:768px) {
    .navigation .menu-button {
        display: block;
        font-size: 2.4rem;
        font-weight: 400;
        line-height: 6rem;
        color: #212121;
        cursor: pointer
    }
    .navigation .menu-button:hover,
    .navigation .menu-button:focus {
        color: #1565c0
    }
}

.pagination {
    margin-top: 6rem;
    text-align: center;
    font-family: Lato, Helvetica, sans-serif
}

.pagination li {
    display: inline;
    text-align: center;
    font-weight: 700
}

.pagination li span {
    margin: 0;
    text-align: center;
    width: 3.2rem
}

.pagination li a {
    font-weight: 300
}

.pagination li a span {
    margin: 0;
    text-align: center;
    width: 3.2rem
}

.footer {
    width: 100%;
    text-align: center;
    line-height: 2rem;
    margin-bottom: 1rem
}

.footer a {
    color: #1565c0
}
body.colorscheme-dark {
    color: #dadada;
    background-color: #212121
}

body.colorscheme-dark a {
    color: #36679f
}

body.colorscheme-dark h1,
body.colorscheme-dark h2,
body.colorscheme-dark h3,
body.colorscheme-dark h4,
body.colorscheme-dark h5,
body.colorscheme-dark h6 {
    color: #dadada
}

body.colorscheme-dark code {
    background-color: #424242;
    color: #dadada
}

body.colorscheme-dark pre code {
    background-color: inherit;
    color: inherit
}

body.colorscheme-dark blockquote {
    border-left: 2px solid #424242
}

body.colorscheme-dark table td,
body.colorscheme-dark table th {
    border: 2px solid #dadada
}

@media(prefers-color-scheme:dark) {
    body.colorscheme-auto {
        color: #dadada;
        background-color: #212121
    }
    body.colorscheme-auto a {
        color: #36679f
    }
    body.colorscheme-auto h1,
    body.colorscheme-auto h2,
    body.colorscheme-auto h3,
    body.colorscheme-auto h4,
    body.colorscheme-auto h5,
    body.colorscheme-auto h6 {
        color: #dadada
    }
    body.colorscheme-auto code {
        background-color: #424242;
        color: #dadada
    }
    body.colorscheme-auto pre code {
        background-color: inherit;
        color: inherit
    }
    body.colorscheme-auto blockquote {
        border-left: 2px solid #424242
    }
    body.colorscheme-auto table td,
    body.colorscheme-auto table th {
        border: 2px solid #dadada
    }
}

body.colorscheme-dark .content .list ul li .title {
    color: #dadada
}

body.colorscheme-dark .content .list ul li .title:hover,
body.colorscheme-dark .content .list ul li .title:focus {
    color: #36679f
}

body.colorscheme-dark .content .centered .about ul li a {
    color: #dadada
}

body.colorscheme-dark .content .centered .about ul li a:hover {
    color: #36679f
}

@media(prefers-color-scheme:dark) {
    body.colorscheme-auto .content .list ul li .title {
        color: #dadada
    }
    body.colorscheme-auto .content .list ul li .title:hover,
    body.colorscheme-auto .content .list ul li .title:focus {
        color: #36679f
    }
    body.colorscheme-auto .content .centered .about ul li a {
        color: #dadada
    }
    body.colorscheme-auto .content .centered .about ul li a:hover {
        color: #36679f
    }
}

body.colorscheme-dark .navigation a,
body.colorscheme-dark .navigation span {
    color: #dadada
}

body.colorscheme-dark .navigation a:hover {
    color: #36679f
}

@media only screen and (max-width:768px) {
    body.colorscheme-dark .navigation .navigation-list {
        background-color: #212121;
        border-top: solid 2px #424242;
        border-bottom: solid 2px #424242
    }
}

@media only screen and (max-width:768px) {
    body.colorscheme-dark .navigation .navigation-list .menu-separator {
        border-top: 2px solid #dadada
    }
}

@media only screen and (max-width:768px) {
    body.colorscheme-dark .navigation #menu-toggle:checked+label {
        color: #424242
    }
}

@media only screen and (max-width:768px) {
    body.colorscheme-dark .navigation .menu-button {
        color: #dadada
    }
    body.colorscheme-dark .navigation .menu-button:hover,
    body.colorscheme-dark .navigation .menu-button:focus {
        color: #36679f
    }
}

@media(prefers-color-scheme:dark) {
    body.colorscheme-auto .navigation a,
    body.colorscheme-auto .navigation span {
        color: #dadada
    }
    body.colorscheme-auto .navigation a:hover {
        color: #36679f
    }
}

@media only screen and (prefers-color-scheme:dark) and (max-width:768px) {
    body.colorscheme-auto .navigation .navigation-list {
        background-color: #212121;
        border-top: solid 2px #424242;
        border-bottom: solid 2px #424242
    }
}

@media only screen and (prefers-color-scheme:dark) and (max-width:768px) {
    body.colorscheme-auto .navigation .navigation-list .menu-separator {
        border-top: 2px solid #dadada
    }
}

@media only screen and (prefers-color-scheme:dark) and (max-width:768px) {
    body.colorscheme-auto .navigation #menu-toggle:checked+label {
        color: #424242
    }
}

@media only screen and (prefers-color-scheme:dark) and (max-width:768px) {
    body.colorscheme-auto .navigation .menu-button {
        color: #dadada
    }
    body.colorscheme-auto .navigation .menu-button:hover,
    body.colorscheme-auto .navigation .menu-button:focus {
        color: #36679f
    }
}

body.colorscheme-dark .footer a {
    color: #36679f
}

@media(prefers-color-scheme:dark) {
    body.colorscheme-auto .footer a {
        color: #36679f
    }
}
